import Card from "@mui/material/Card";
import s from "./Cart.module.css";
import CardMedia from "@mui/material/CardMedia";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import RemoveShoppingCartIcon from '@mui/icons-material/RemoveShoppingCart';
import React, {useContext} from "react";
import {Checkbox} from "@mui/material";
import AddShoppingCartIcon from "@mui/icons-material/AddShoppingCart";
import {Context} from "../../../index";
import {observer} from "mobx-react-lite";

type ProductCardProps ={
    image:string,
    alt: string,
    head: string,
    description:string,
    price: number
    productId:string
}
const ProductCard = observer((props: ProductCardProps)=> {
    const {store}=useContext(Context);
    return (
        <Card className={s.productCard} >
            <div style={{display:"flex", alignItems:"center"}}><Checkbox
                checked={false}
                onChange={()=>{}}
                inputProps={{ 'aria-label': 'controlled' }}
            />
            </div>
            <div style={{display: "flex", justifyContent: "space-between", alignItems:"center", width:"100%" , cursor: 'pointer'}}>
                <div>
                    <CardMedia
                        component="img"
                        height="100px"
                        image={props.image}
                        alt={props.alt}
                    />
                </div>
                <div style={{display: "flex", justifyContent: "space-around", width: "100%"}}>
                    <Typography gutterBottom variant="h6" component="div">
                        {props.head}
                    </Typography>
                    <Typography variant="body2" sx={{color: 'text.secondary'}}>
                        {props.description}
                    </Typography>
                    <Typography variant="body2" sx={{color: 'text.secondary'}}>
                        {props.price} $
                    </Typography>
                </div>
                <div style={{display: "flex", flexDirection:"column", justifyContent: "space-between", alignItems:"center"}}>
                    <Button size="small" color="success" variant="contained">
                        
                    </Button>
                    <Checkbox icon={<AddShoppingCartIcon />} checkedIcon={<RemoveShoppingCartIcon />} color={'error'} checked={store.cartProducts.includes(props.productId)}
                              onChange={() => {
                                  store.cartProducts.includes(props.productId)
                                      ? store.removeProductInCart(props.productId)
                                      : store.addInCart(props.productId);
                              }} />
                </div>
            </div>

        </Card>
    );
})
export default ProductCard;