import React, {useContext} from 'react';
import Card from '@mui/material/Card';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import s from './Products.module.css'
import AddShoppingCartIcon from '@mui/icons-material/AddShoppingCart';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import {Favorite, FavoriteBorder} from "@mui/icons-material";
import {Checkbox} from "@mui/material";
import {Context} from "../../../index";
import {observer} from "mobx-react-lite";

type SaleCardProps ={
    image:string,
    alt: string,
    head: string,
    description:string,
    price: number,
    productId:string
}
const SaleCard =observer((props: SaleCardProps) =>{
    const {store}=useContext(Context);
    return (
        <Card className={s.productCard} >
            <div>
                <CardMedia
                    component="img"
                    height="200px"
                    image={props.image}
                    alt={props.alt}
                />
                <div>
                    <Typography gutterBottom variant="h5" component="div">
                        {props.head}
                    </Typography>
                    <Typography variant="body2" sx={{ color: 'text.secondary' }}>
                        {props.description}
                    </Typography>
                    <Typography variant="body2" sx={{ color: 'text.secondary' }}>
                        {props.price} $
                    </Typography>
                </div>
            </div>
            <div style={{display: 'flex', justifyContent: 'space-around', alignItems: 'flex-end'}}>
                <Checkbox icon={<FavoriteBorder />} checkedIcon={<Favorite />} color={'error'} checked={store.likedProducts.includes(props.productId)}
                          onChange={() => {
                              store.likedProducts.includes(props.productId)
                                  ? store.removeLike(props.productId)
                                  : store.addLike(props.productId);
                          }} />
                <Button size="small" color="success" variant="outlined" >
                    
                </Button>
                <Checkbox icon={<AddShoppingCartIcon />} checkedIcon={<ShoppingCartIcon />} color={'success'} checked={store.cartProducts.includes(props.productId)}
                          onChange={() => {
                              store.cartProducts.includes(props.productId)
                                  ? store.removeProductInCart(props.productId)
                                  : store.addInCart(props.productId);
                          }} />
            </div>
        </Card>
    );
})
export default SaleCard