import {IUser} from "../models/IUser";
import {makeAutoObservable} from "mobx";
import {IProduct, IStore} from "../models/IStore";
import StoreService from "../services/StoreService";
import {API_URL} from "../http";
import {AuthResponse} from "../models/response/AuthResponse";
import axios from "axios";
import AuthService from "../services/AuthService";
export default class Store {
    user = {} as IUser;
    store = {} as IStore;
    isAuth = false;
    isLoading = false;
    likedProducts: string[] = [];
    cartProducts: string[] = [];
    searchQuery: string = "";



    constructor() {
        makeAutoObservable(this);
        this.loadState()
        this.loadLikes();
        this.loadProductsInCart()
    }
    setSearchQuery(query: string) {
        this.searchQuery = query;
    }
    setUser(user: IUser) {
        this.user = user;
        this.saveState();
    }
    setLoading(isLoading: boolean) {
        this.isLoading = isLoading;
    }

    setAuth(bool: boolean) {
        this.isAuth = bool;
        this.saveState();
    }
    setStore(store: IStore) {
        this.store=store;
        this.saveState();
    }
    saveState() {
        const state = {
            user: this.user,
            stores: this.store,
        };
        localStorage.setItem("appState", JSON.stringify(state));
    }
    loadState() {
        const savedState = localStorage.getItem("appState");
        if (savedState) {
            const { user, isAuth } = JSON.parse(savedState);
            this.user = user;
            this.isAuth = isAuth;
        }
    }
    addLike(productId: string) {
        if (!this.likedProducts?.includes(productId)) {
            this.likedProducts.push(productId);
            this.saveLikes();
        }
    }

    removeLike(productId: string) {
        this.likedProducts = this.likedProducts.filter(id => id !== productId);
        this.saveLikes();
    }

    saveLikes() {
        if (!this.isAuth) {
            localStorage.setItem("likedProducts", JSON.stringify(this.likedProducts));
        } else {
            StoreService.saveLikes(this.likedProducts);
        }
    }
    loadLikes() {
        const likes = localStorage.getItem("likedProducts");
        if (likes) {
            this.likedProducts = JSON.parse(likes);
        }
    }
    addInCart(productId: string) {
        if (!this.cartProducts?.includes(productId)) {
            this.cartProducts.push(productId);
            this.saveProductsInCart();
        }
    }

    removeProductInCart(productId: string) {
        this.cartProducts = this.cartProducts.filter(id => id !== productId);
        this.saveProductsInCart();
    }

    saveProductsInCart() {
        if (!this.isAuth) {
            localStorage.setItem("cartProducts", JSON.stringify(this.cartProducts));
        } else {
           StoreService.saveProductsInCart(this.cartProducts);
        }
    }
    loadProductsInCart() {
        const products = localStorage.getItem("cartProducts");
        if (products) {
            this.cartProducts = JSON.parse(products);
        }
    }
    async getStore(storeId: any) {
        try {
            const response = await StoreService.getStore(storeId);
            this.setLoading(true)
            this.setStore(response.data);
        } catch (error) {
            console.error('   :', error);
        } finally {
            this.setLoading(false)
        }
    }
    async login(email: string, password: string) {
        try {
            const response = await AuthService.login(email, password);
            localStorage.setItem("token", response.data.accessToken);
            this.setAuth(true);
            this.setUser(response.data.user);
            await this.syncLikesWithServer();
            await this.syncCartProductsWithServer()
            return response
        } catch (e: any) {
            console.log(e.response?.data?.message);
        }
    }

    async registration(role:string, email: string, password: string, firstName: string, lastName: string, phoneNumber: string) {
        try {
            const response = await AuthService.registration(role, email, password, firstName, lastName, phoneNumber);
            localStorage.setItem("token", response.data.accessToken);
            this.setAuth(true);
            this.setUser(response.data.user);
            await this.syncLikesWithServer();
            await this.syncCartProductsWithServer()
            return response
        } catch (e: any) {
            console.log(e.response?.data?.message);
        }
    }

    async logout() {
        try {
           const response = await AuthService.logout();
            localStorage.removeItem("token");
            localStorage.removeItem("appState");
            this.setAuth(false);
            this.setUser({} as IUser);
            return response
        } catch (e: any) {
            console.log(e.response?.data?.message);
        }

    }

    async checkAuth() {
        try {
            const response = await axios.get<AuthResponse>(`${API_URL}/refresh`, {withCredentials: true});
            localStorage.setItem('token', response.data.accessToken)
            this.setAuth(true);
            this.setUser(response.data.user);
            this.setLoading(true)
            await this.syncLikesWithServer();
            await this.syncCartProductsWithServer()
            return response
        } catch (e: any) {
            console.log(e.response?.data?.message);
        } finally {
            this.setLoading(false)
        }
    }

    async forgotPassword(email: string) {
        try {
            const response = await AuthService.forgotPassword(email)
            return response
        } catch (e: any) {
            console.log(e.response?.data?.message);
        }

    }
    async resetPassword(token: any, newPassword: string) {
        try {
            const response =await AuthService.resetPassword(token, newPassword);
            return response
        } catch (e: any) {
            console.log(e.response?.data?.message);
        }
    }

    async syncLikesWithServer() {
        try {
            const response = await StoreService.getLikes();
            const serverLikes = response.data.likes;
            const localLikes = localStorage.getItem("likedProducts");
            if (localLikes) {
                const parsedLocalLikes = JSON.parse(localLikes);
                const mergedLikes = Array.from(new Set([...serverLikes, ...parsedLocalLikes]));
                await StoreService.mergeLikes(mergedLikes);
                localStorage.removeItem("likedProducts");
                this.likedProducts = mergedLikes;
            } else {
                this.likedProducts = serverLikes;
            }
        } catch (e) {
            console.error('   :', e);
        }
    }
    async syncCartProductsWithServer() {
        try {
            const response = await StoreService.getProductsInCart();
            const serverProducts = response.data.products;
            const localProducts = localStorage.getItem("cartProducts");
            if (localProducts) {
                const parsedLocalProducts = JSON.parse(localProducts);
                const mergedProducts = Array.from(new Set([...serverProducts, ...parsedLocalProducts]));
                await StoreService.mergeProductsInCart(mergedProducts);
                localStorage.removeItem("cartProducts");
                this.cartProducts = mergedProducts;
            } else {
                this.cartProducts = serverProducts;
            }
        } catch (e) {
            console.error('     :', e);
        }
    }
}

