import { useSearchParams, useNavigate } from 'react-router-dom';
import React, {useContext, useState} from 'react';
import styles from "./LoginPage.module.css";
import {Button, FormHelperText, TextField} from "@mui/material";
import {useTranslation} from "react-i18next";
import {Context} from "../../../index";
import LanguageSelect from "../../LanguageSelect";
import {PasswordForm} from "../../PasswordForm";

export const ResetPasswordPage = () => {
    const {store} = useContext(Context);
    const [searchParams] = useSearchParams();
    const token = searchParams.get("token");
    const navigate = useNavigate();
    const { t } = useTranslation();
    const [resetForm, setResetForm] = useState({
        email: searchParams.get("email"),
        password1: "",
        password2: "",
    });
    const [passwordError, setPasswordError] = useState("");
    const handlePasswordChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value } = e.target;
        setResetForm(prevState => ({ ...prevState, [name]: value }));

        if (name === "password1" || name === "password2") {
            if (value.length < 8) {
                setPasswordError(t('passwordLengthError'));
            } else if (name === "password2" && value !== resetForm.password1) {
                setPasswordError(t('passwordMismatchError'));
            } else {
                setPasswordError("");
            }
        }
    };
    const resetHandler =()=>{
        if (!passwordError) {
            store.resetPassword(token, resetForm.password1);

            navigate("/ShopData/");
        }
    }

    return (
        <div className={styles.container}>
            <div className={styles.addForm}>
                <LanguageSelect white={true}/>
                <TextField
                    label={t('emailLabel')}
                    value={resetForm.email}
                    className={styles.inputField}
                    sx={textFild}
                />
                <PasswordForm
                    passwordError={passwordError}
                    label={t('passwordLabel')}
                    handlePasswordChange={handlePasswordChange}
                    password={resetForm.password1}
                    name={'password1'}
                />
                <PasswordForm
                    passwordError={passwordError}
                    label={t('repeatPasswordLabel')}
                    handlePasswordChange={handlePasswordChange}
                    password={resetForm.password2}
                    name={'password2'}
                />
                {passwordError && <FormHelperText error>{passwordError}</FormHelperText>}
                <Button
                    variant="contained"
                    onClick={resetHandler}
                    className={styles.createButton}
                >
                    {t('changePassword')}
                </Button>
                <Button
                    variant="contained"
                    onClick={() => navigate('/login')}
                    className={styles.cancelButton}
                >
                    {t('cancel')}
                </Button>
            </div>
        </div>
    );
};
const textFild = {

    '& .MuiOutlinedInput-root': {
        backgroundColor: 'transparent',
        borderColor: 'white',
    },
    '& .MuiInputBase-input': {
        color: 'white',
    },
    '& .MuiInputLabel-root': {
        color: 'white',
    },
    '& .MuiOutlinedInput-notchedOutline': {
        borderColor: 'white',
    },
}