import SaleCard from "../Products/SaleCard";
import {useContext} from "react";
import {Context} from "../../../index";
import {useTranslation} from "react-i18next";


export const LikePage = ()=>{
const {store}= useContext(Context);
const {t}= useTranslation();
    const likedProducts = store.store.rowsAll.filter(p => store.likedProducts.includes(p.id))
    return (
        <div>
            <div>
                <h1 style={{display:"flex", justifyContent:"flex-start", paddingLeft:"10px"}}>{t('listOfLikes')}: </h1>
                <div style={{display:'flex', paddingLeft: '30px', paddingBottom:'30px'}}>
                    {likedProducts.map(p => (
                        <SaleCard  key={p.id} head={p.name} price={p.sellingPrice} productId={p.id} alt={p.name} description={p.description} image={p.image}/>
                        ))}
                      </div>
            </div>
        </div>
    )
}