
import $api from "../http";

export default class StoreService {
    static async getStore(storeId:any) {
        return $api.post('/getStore', { storeId });
    }
    static async mergeLikes(likes:any) {
        return $api.post('/mergeLikes', {likes});
    }
    static async saveLikes(likes:any) {
        return $api.post('/saveLikes', {likes});
    }
    static async getLikes() {
        return $api.get('/getLikes');
    }
    static async mergeProductsInCart(products:any) {
        return $api.post('/mergeProductsInCart', {products});
    }
    static async saveProductsInCart(products:any) {
        return $api.post('/saveProductsInCart', {products});
    }
    static async getProductsInCart() {
        return $api.get('/getProductsInCart');
    }

}
