import {useContext, useEffect} from "react";
import {useNavigate} from "react-router-dom";
import {Context} from "../../../index";
import {Button} from "@mui/material";
import {useTranslation} from "react-i18next";
import styles from "./Profile.module.css"

export const Profile = ()=>{
    const { store } = useContext(Context);
    const {t} = useTranslation()
    const navigate = useNavigate();
    const logOut = async ()=>{
        try {
            await store.logout()
            navigate("/login")
        } catch (e) {
            console.log(e);
        }

    }
    useEffect(() => {
        if (!store.isAuth) {
            navigate("/login");
        }
    }, [store.isAuth, navigate]);
    return (
        <div className={styles.profileContainer}>
        <div className={styles.profileCard}>
            <div style={{display: 'flex', justifyContent: 'flex-end', paddingRight: '50px'}}>
                <Button onClick={logOut} color={'error'} variant={'outlined'}>{t('logout')}</Button>
            </div>

            <h1 style={{display: "flex", justifyContent: "flex-start", paddingLeft: "10px"}}>{t('personalInfo')}:</h1>
            <h3 style={{display: "flex", justifyContent: "flex-start", paddingLeft: "10px"}}>{store.user.isActivated ? " " + t('profileConfirmed') : " " + t('confirmEmail')}</h3>
            <div className={styles.list}>
                <h3>{t('firstName')}: {store.user.firstName}</h3>
            </div>
            <div className={styles.list}>
                <h3>{t('lastName')}: {store.user.lastName}</h3>
            </div>
            <div className={styles.list}>
                <h3>{t('email')}: {store.user.email}</h3>
            </div>
            <div className={styles.list}>
                <h3>{t('phone')}: {store.user.phoneNumber}</h3>
            </div>
            <div className={styles.list} style={{cursor: 'pointer'}} onClick={()=>navigate("/likes")}>
                <h3>{t('listOfLikes')}: {store.likedProducts.length-1}</h3>
            </div>
            <div className={styles.list} style={{cursor: 'pointer'}} onClick={()=>navigate("/cart")}>
                <h3>{t('listOfProductCard')}: {store.cartProducts.length-1}</h3>
            </div>
            <div className={styles.list}  >
                <h3>{t('ListOfOrders')}: 0 ??</h3>
            </div>
        </div>
        </div>
    )
}