import React, {useContext, useEffect, useState} from "react";
import s from './NavBar.module.css'
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import ShoppingCartOutlinedIcon from '@mui/icons-material/ShoppingCartOutlined';
import PersonOutlineIcon from '@mui/icons-material/PersonOutline';
import {Button} from "@mui/material";
import {useNavigate} from "react-router-dom";
import {Context} from "../../index";

export const Navbar =()=> {
    const {store}=useContext(Context);
    const [loading, setLoading] = useState(true);
    const navigate = useNavigate();

    useEffect(() => {
        store.getStore("").then(() => {
            setLoading(false);
        });
    }, []);
    if (loading) return <p>...</p>;
    return <div className={s.navbar}>

        <div>
            <Button variant="text" color={'inherit'} onClick={()=>navigate('/AutoShop')}></Button>
            <Button variant="text" color={'inherit'} onClick={()=>navigate('/products')}></Button>
        </div>

        <div></div>
        <div></div>
        <div className={s.navDivRight}>
            <Button onClick={()=>navigate('/likes')} color={'inherit'} size={'small'} ><FavoriteBorderIcon/></Button>
            <Button onClick={()=>navigate('/cart')} color={'inherit'} size={'small'}><ShoppingCartOutlinedIcon/></Button>
            <Button onClick={()=>navigate('/profile')} color={'inherit'} size={'small'}><PersonOutlineIcon/></Button>
        </div>


    </div>
}