import ProductCard from "./ProductCard";
import {useContext} from "react";
import {Context} from "../../../index";
import {useTranslation} from "react-i18next";


export const CartPage = ()=>{
    const {store}=useContext(Context);
    const {t}=useTranslation();
    const productsInCart = store.store.rowsAll.filter(p => store.cartProducts.includes(p.id))
    return (
        <div>
<div>
    <h1 style={{display:"flex", justifyContent:"flex-start", paddingLeft:"10px"}}>{t('listOfProductCard')}: </h1>
    <div style={{display:'flex', flexDirection: 'column', paddingLeft: '30px', paddingBottom:'30px'}}>
        {productsInCart.map(p => (<ProductCard key={p.id} image={p.image} alt={p.name} head={p.name} description={p.description} price={p.sellingPrice} productId={p.id}/> ))}
        </div>
</div>
        </div>
    )
}